import styled from "styled-components";
import { inject, observer } from "mobx-react";
import "./contractInfo.less";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import Type from "../../../model/Type";
import Category from "../../../model/Category";
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import { bg } from "../../../lib/web3js";
import { useMemo } from "react";

const TokenWrapper = styled.span`
  margin-right: 4px;
  display: flex;
`;
function ContractInfo({ lang, trading, getLang }) {
  const contract = useMemo(() => trading.contract, [trading.contract]);
  const basetokenDisplay = (bTokenSymbol = []) => {
    return bTokenSymbol.length === 1 ? (
      bTokenSymbol[0]
    ) : (
      <UnderlineText
        id="btoken"
        width="375"
        tip={bTokenSymbol
          .map((token, index) => `${token} (${contract.bTokenMultiplier[index]})`)
          .join(" ")}
      >
        {bTokenSymbol.map((token, index) => {
          return index < 3 ? (
            <TokenWrapper key={index}>
              {token}({contract.bTokenMultiplier[index]})
            </TokenWrapper>
          ) : null;
        })}
        ...
      </UnderlineText>
    );
  };
  let num = { num: `${bg(contract["maintenanceMarginRatio"]).times(100).toFixed(2)}%` };
  const rows = useMemo(() => {
    if (Category.isFutures) {
      return [
        {
          label: lang["base-token"],
          labelDes: lang["discount-factor"],
          tip: lang["basetoken-multiplier-hover"],
          key: "baseToken",
          value: basetokenDisplay(contract.bTokenSymbol),
        },
        { label: lang["symbol"], key: "symbol", value: contract["displaySymbol"] },
        {
          label: lang["min-trade-volume"],
          key: "minTradeVolume",
          value: trading.contract["minTradeUnit"]
            ? trading.contract["minTradeUnit"] + " " + trading.symbolInfo.unit
            : "",
        },
        {
          label: lang["funding-rate-coefficent"],
          key: "fundingRateCoefficient",
          value:
            contract["fundingRateCoefficient"] &&
            (+contract["fundingRateCoefficient"]).toExponential(),
          valueTip: trading.fundingCoefficientTip,
          tipWidth: 375,
        },
        {
          label: lang["initial-margin-ratio"],
          key: "minInitialMarginRatio",
          tip: lang["initial-margin-ratio-hover"],
          value: (
            <DeriNumberFormat
              value={contract["initialMarginRatio"] * 100}
              decimalScale={0}
              suffix="%"
            />
          ),
        },
        {
          label: lang["maintenance-margin-ratio"],
          key: "minMaintenanceMarginRatio",
          tip: lang["maintenance-margin-ratio-hover"],
          value: (
            <DeriNumberFormat
              value={contract["maintenanceMarginRatio"] * 100}
              decimalScale={0}
              suffix="%"
            />
          ),
        },
        {
          label: lang["transaction-fee"],
          key: "feeRatio",
          value: <DeriNumberFormat value={contract["feeRatio"] * 100} suffix="%" />,
        },
      ];
    } else if (Category.isPower) {
      return [{
        label: lang["base-token"],
        labelDes: lang["discount-factor"],
        tip: lang["basetoken-multiplier-hover"],
        key: "baseToken",
        value: basetokenDisplay(contract.bTokenSymbol),
      },
      { label: lang["symbol"], key: "symbol", value: contract["displaySymbol"] },
      {
        label: lang["min-trade-volume"],
        key: "minTradeVolume",
        value: trading.contract["minTradeUnit"]
          ? trading.contract["minTradeUnit"] + " " + trading.symbolInfo.unit
          : "",
      },
      {
        label: lang["funding-rate-coefficent"],
        key: "fundingRateCoefficient",
        value:
          contract["fundingRateCoefficient"] &&
          (+contract["fundingRateCoefficient"]).toExponential(),
        valueTip: trading.fundingCoefficientTip,
        tipWidth: 375,
      },
      {
        label: lang["initial-margin-ratio"],
        key: "minInitialMarginRatio",
        tip: lang["initial-margin-ratio-hover"],
        value: (
          <DeriNumberFormat
            value={contract["initialMarginRatio"] * 100}
            decimalScale={0}
            suffix="%"
          />
        ),
      },
      {
        label: lang["maintenance-margin-ratio"],
        key: "minMaintenanceMarginRatio",
        tip: lang["maintenance-margin-ratio-hover"],
        value: (
          <DeriNumberFormat
            value={contract["maintenanceMarginRatio"] * 100}
            decimalScale={0}
            suffix="%"
          />
        ),
      },
      {
        label: lang["transaction-fee"],
        key: "feeRatio",
        value: <DeriNumberFormat value={contract["feeRatio"] * 100} suffix="%" />,
      }, {
        label: lang["multiplier"],
        key: "displayMultiplier",
        value: <DeriNumberFormat value={trading.contract.displayMultiplier} decimalScale={-1} />,
        tip: trading.powerMultiplierTip,
      },
      {
        label: lang["funding-period"],
        key: "feeRatio",
        value: (
          <DeriNumberFormat
            value={bg(contract["fundingPeriod"]).div(86400).toFixed(0)}
            suffix={` ${lang["days"]}`}
          />
        ),
        tip: getLang("powers-funding-period-hover", { symbol: trading.symbolInfo.symbol }),
        tipHtml: true,
        element: (
          <span className="tip-width">
            <div>
              {getLang("powers-funding-period-hover", { symbol: trading.symbolInfo.symbol })}
            </div>{" "}
            <div>
              {/* <a target="_blank" rel='noreferrer' href='https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559'> {lang['learn-more']}</a> */}
            </div>
          </span>
        ),
      }]
    } else if (Category.isGamma) {
      const fields = [{
        label: lang["base-token"],
        labelDes: lang["discount-factor"],
        tip: lang["basetoken-multiplier-hover"],
        key: "baseToken",
        value: basetokenDisplay(contract.bTokenSymbol),
      },
      { label: lang["symbol"], key: "symbol", value: contract["displaySymbol"] },
      {
        label: lang["min-trade-volume"],
        key: "minTradeVolume",
        value: trading.contract["minTradeUnit"]
          ? trading.contract["minTradeUnit"] + " " + trading.symbolInfo.unit
          : "",
      },
      // {
      //   label: lang["funding-rate-coefficent"],
      //   key: "fundingRateCoefficient",
      //   value:
      //     contract["fundingRateCoefficient"] &&
      //     (+contract["fundingRateCoefficient"]).toExponential(),
      //   valueTip: trading.fundingCoefficientTip,
      //   tipWidth: 375,
      // },
      {
        label: lang["initial-margin-ratio"],
        key: "minInitialMarginRatio",
        tip: lang["initial-margin-ratio-hover"],
        tipHtml: true,
        element: (
          <span className="tip-width">
            Initial Margin Per Volume:(Delta * Underlier Price Change + 1/2 * Gamma * Underlier Price Change²) = {+contract["initialMarginPerVolume"] && contract && +contract["initialMarginPerVolume"].toFixed(0)}, with Underlier Price Change being {(+contract["originInitialMarginRatio"])} * index price. Initial Margin Ratio is defined as Initial Margin Per Volume divided by the contract value of  Gamma Swap, which is the same as the cost of trading an equivalent amount of {trading.symbolInfo.powerUnderlier}.
            <a target="_blank" rel='noreferrer' href='https://im0xalpha.notion.site/Gamma-Swap-edf0ca3382ac46e3bfdbec5001a4d923'> {lang['learn-more']}</a>
          </span>
        ),
        value: (
          <DeriNumberFormat
            value={contract["initialMarginRatio"] * 100}
            decimalScale={2}
            suffix="%"
          />
        ),
      },
      {
        label: lang["maintenance-margin-ratio"],
        key: "minMaintenanceMarginRatio",
        tip: lang["gamma-maintenance-margin-ratio-hover"],
        value: (
          <DeriNumberFormat
            value={contract["maintenanceMarginRatio"] * 100}
            decimalScale={2}
            suffix="%"
          />
        ),
      },
      {
        label: lang["transaction-fee"],
        key: "feeRatio",
        value: <>
          <UnderlineText tip={`the contract value is the cost of trading an equivalent amount of  ${trading.symbolInfo.powerUnderlier}`}>
            Contract value
          </UnderlineText>
          &nbsp;
          <DeriNumberFormat prefix=" * " value={contract["feeRatio"] * 100} suffix="%" />
        </>,
      }, {
        label: lang["multiplier"],
        key: "displayMultiplier",
        value: <DeriNumberFormat value={trading.contract.displayMultiplier} decimalScale={-1} />,
        tip: trading.powerMultiplierTip,
      },
      {
        label: lang["funding-period"],
        key: "feeRatio",
        value: (
          <DeriNumberFormat
            value={bg(contract["fundingPeriod"]).div(86400).toFixed(0)}
            suffix={` ${lang["days"]}`}
          />
        ),
        // tip: getLang("powers-funding-period-hover", { symbol: trading.symbolInfo.symbol }),
        // tipHtml: true,
        // element: (
        //   <span className="tip-width">
        //     <div>
        //       {getLang("powers-funding-period-hover", { symbol: trading.symbolInfo.symbol })}
        //     </div>{" "}
        //     <div>
        //       {/* <a target="_blank" rel='noreferrer' href='https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559'> {lang['learn-more']}</a> */}
        //     </div>
        //   </span>
        // ),
      }];
      if(!trading.symbolInfo.hasSpecialFormat) {
        const pos = fields.findIndex(f => f.key === 'displayMultiplier')
        if(pos > -1){
          fields.splice(pos,1)
        }
      }
      return fields;
    } else {
      return [
        {
          label: lang["base-token"],
          labelDes: lang["discount-factor"],
          tip: lang["basetoken-multiplier-hover"],
          key: "baseToken",
          value: basetokenDisplay(contract.bTokenSymbol),
        },
        { label: lang["symbol"], key: "symbol", value: contract["displaySymbol"] },
        { label: lang["underlier"], key: "symbol", value: contract["underlier"] },
        { label: lang["strike"], key: "symbol", value: contract["strike"] },
        { label: lang["option-type"], key: "symbol", value: contract["optionType"] },
        {
          label: lang["min-trade-volume"],
          key: "minTradeVolume",
          value: trading.contract["minTradeUnit"]
            ? trading.contract["minTradeUnit"] + trading.symbolInfo.unit
            : "",
        },
        {
          label: lang["initial-margin-ratio"],
          tip: `${lang["initial-margin-ratio-hover"]} <br> ${lang["option-initial-margin-ratio-formula"]
            } ${bg(contract["maintenanceMarginRatio"]).times(200).toString()}%`,
          key: "minInitialMarginRatio",
          value: (
            <DeriNumberFormat
              value={contract["initialMarginRatio"] * 100}
              decimalScale={2}
              suffix="%"
            />
          ),
        },
        {
          label: lang["maintenance-margin-ratio"],
          tip: (() => {
            return `${lang["maintenance-margin-ratio-hover"]} <br> ${getLang(
              "maintenance-margin-ratio-1",
              num
            )} ${trading.index} * 2% = ${bg(trading.index).times(0.02)} `;
          })(),
          tipHtml: true,
          key: "maintenanceMarginRatio",
          element: (
            <span className="tip-width">
              <div>{lang["maintenance-margin-ratio-hover"]}</div>
              {(() => {
                return ` ${getLang("maintenance-margin-ratio-1", num)} ${trading.index} * 2% = ${bg(
                  trading.index
                ).times(0.02)} `;
              })()}
              <div>
                <a
                  target="_blank"
                  rel="noreferrer"
                  href="https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559"
                >
                  {" "}
                  {lang["learn-more"]}
                </a>
              </div>
            </span>
          ),
          value: (
            <DeriNumberFormat
              value={contract["maintenanceMarginRatio"] * 100}
              decimalScale={2}
              suffix="%"
            />
          ),
        },
        {
          label: lang["transaction-fee"],
          tip: trading.TransactionFeeTip,
          tipWidth: "320",
          key: "feeRatio",
          value: `${trading.feeDisplay.join(" ")}%`,
        },
        {
          label: lang["funding-period"],
          key: "feeRatio",
          value: (
            <DeriNumberFormat
              value={bg(contract["fundingPeriod"]).div(86400).toFixed(0)}
              suffix={` ${lang["days"]}`}
            />
          ),
          tip: lang["funding-period-hover"],
          tipHtml: true,
          element: (
            <span className="tip-width">
              <div>{lang["funding-period-hover"]}</div> {lang["funding-period-hover-two"]}{" "}
              <div>
                {/* <a target="_blank" rel='noreferrer' href='https://deri-protocol.medium.com/changes-to-everlasting-options-81e446768559'> {lang['learn-more']}</a> */}
              </div>
            </span>
          ),
        },
        {
          label: lang["delta"],
          key: "delta",
          value: <DeriNumberFormat value={contract["delta"]} decimalScale="2" />,
        },
        {
          label: lang["gamma"],
          key: "gamma",
          value: <DeriNumberFormat value={contract["gamma"]} decimalScale="6" />,
        },
      ];
    }
  }, [contract, lang, trading.symbolInfo, num])
  return (
    <div className="contract-info">
      <div className="c-title">{lang["contract-info"]}</div>
      <div className="c-body">
        {rows.map(
          (row, index) =>
            row.value && (
              <div className="c-row" key={index}>
                <div>
                  {row.tip && !row.labelDes ? (
                    <UnderlineText
                      id={row.label}
                      html={row.tipHtml}
                      element={row.element}
                      text={row.label}
                      multiline={true}
                      tip={row.tip}
                      width={row.tipWidth}
                    />
                  ) : row.labelDes ? (
                    <span className="base">
                      {row.label} (
                      <UnderlineText
                        text={row.labelDes}
                        multiline={true}
                        block="inline-block"
                        id={row.label}
                        tip={row.tip}
                        width={row.tipWidth}
                      />
                      ){" "}
                    </span>
                  ) : (
                    row.label
                  )}
                </div>
                <div className="c-value">
                  {row.valueTip ? (
                    <UnderlineText
                      text={row.value}
                      multiline={true}
                      tip={row.valueTip}
                      width={row.tipWidth}
                    />
                  ) : (
                    row.value
                  )}
                </div>
              </div>
            )
        )}
      </div>
    </div>
  );
}
export default inject("trading")(observer(ContractInfo));
